
import { defineComponent, onMounted, ref, watch } from "vue";
import { WhitelistType } from "@gemworks/gem-farm-ts";
import useCluster from "@/composables/cluster";
import useWallet from "@/composables/wallet";
import { PublicKey } from "@solana/web3.js";
import { initGemFarm } from "@/common/gem-farm";
import { listenerCount } from "process";

export default defineComponent({
  props: {
    farm: { type: String, required: true },
    bank: { type: String, required: true },
  },

  setup(props, ctx) {
    const { wallet, getWallet } = useWallet();
    const { cluster, getConnection } = useCluster();

    let gf: any;
    watch([wallet, cluster], async () => {
      gf = await initGemFarm(getConnection(), getWallet()!);
    });

    //need an onmounted hook because this component isn't yet mounted when wallet/cluster are set
    onMounted(async () => {
      if (getWallet() && getConnection()) {
        gf = await initGemFarm(getConnection(), getWallet()!);
      }
      await fetchProofs();
    });

    // --------------------------------------- whitelist
    const action = ref<string>("add");
    const address = ref<string>();
    const type = ref<WhitelistType>(WhitelistType.Creator);
    const proofs = ref<PublicKey[]>([]);
    const hashlist = ref<string>("");

    const previewFiles = async (event: any) => {
      const files: FileList = event.target.files;
      hashlist.value = "";
      for (let i = 0; i < files.length; ++i) {
        const file = files[i];
        const reader = new FileReader();
        reader.onload = (res) => {
          hashlist.value += res!.target!.result;
          hashlist.value += "\n";
        };
        reader.onerror = (err) => console.log(err);
        reader.readAsText(file);
      }
    };

    const fetchProofs = async () => {
      proofs.value = await gf.fetchAllWhitelistProofPDAs(
        new PublicKey(props.bank)
      );
    };

    const updateWhitelist = async () => {
      if (hashlist.value != "") {
        const addressArr = hashlist.value.split("\n")
          .map(line => line.trim())
          .filter(line => line.length > 0)
          .map(line => new PublicKey(line));

        if (action.value === "add") {
          await gf.addMultipleToBankWhitelistWallet(
            getWallet(),
            getConnection(),
            new PublicKey(props.farm),
            addressArr,
            type.value
          );
          await fetchProofs();
        } else {
          await gf.removeMultipleFromBankWhitelistWallet(
            getWallet(),
            getConnection(),
            new PublicKey(props.farm),
            addressArr
          );
          await fetchProofs();
        }
      } else {
        if (action.value === "add") {
          await gf.addToBankWhitelistWallet(
            new PublicKey(props.farm),
            new PublicKey(address.value!),
            type.value
          );
          await fetchProofs();
        } else {
          await gf.removeFromBankWhitelistWallet(
            new PublicKey(props.farm),
            new PublicKey(address.value!)
          );
          await fetchProofs();
        }
      }
    };

    const parseWhitelistType = (numType: number) => {
      switch (numType) {
        case 1:
          return "Creator";
        case 2:
          return "Mint";
        case 3:
          return "Mint + Whitelist";
        default:
          return "unknown";
      }
    };

    return {
      WhitelistType,
      action,
      address,
      type,
      proofs,
      hashlist,
      previewFiles,
      updateWhitelist,
      parseWhitelistType,
    };
  },
});
